<template>
    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left" id="divImportLeadDatakData" v-cloak>
        <loader :is-visible="isLoading"></loader>
        <div id="divshowImportLeadList" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-3 float-left p-0">
            <div class="row form-head">
                <div class="col-sm-12 col-md-6 col-lg-8 col-xl-8 float-left"><span
                        class="form-title">{{$t('ImportLeads')}} {{$t('Detail')}} </span></div>
                <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 float-left">
                    <div class="userguide-box d-none">
                        <a class="userguide-btn" data-toggle="collapse" href="#user-guide" aria-expanded="false"
                            aria-controls="search-content"><em 
                                class="fa"></em><span>{{$t('User_Guide')}}</span></a>
                        <div class="clearfix"></div>
                    </div>
                </div>
            </div>
            <Form v-slot="{  valid,errors}" ref="form">
            <div class="listing">
                <div class="table-responsive table-fix-header" style="min-height: 600px">
                    <table class="table table-bordered dt-responsive nowrap mt-0" aria-describedby="leadimportdetail">
                        <thead class="thead-bg">
                            <tr class="text-capitalize">
                                 <th scope="col" v-for="(item,itemIndex) in Headers" :key="itemIndex" style="min-width: 200px"  >
                                    <span style="display:inline-block !important"
                                        v-bind:data-column_name="item.TABLE_COLUMN_NAME"
                                        v-bind:data-display_name="item.DISPLAY_NAME">{{GetFormSchemaFieldName(item.TABLE_COLUMN_NAME,item.DISPLAY_NAME)}}</span>
                                    <span v-bind:class="{'text-danger':item.IS_REQUIRED}"
                                        v-if="item.IS_REQUIRED" style="display:inline-block !important">*</span>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item,index) in ImportLeadData" :key="index">
                                <td v-for="(key, KIndex) in Headers" :key="KIndex" class="text-center" style="width: 100px;overflow:visible !important"
                                    >
                                    <div class="input-group table_datepickerfix" v-if="key.ACTUAL_DATA_TYPE=='date'">
                                        <Field :name="GetFormSchemaFieldName(key.COLUMN_NAME,key.DISPLAY_NAME)" :rules="{ 'required': key.IS_REQUIRED }"
                                         v-slot="{ errors, changed }">
                                         <v-date-picker :minute-increment="5"  class="input-group"
                                            v-model="item[key.COLUMN_NAME]"
                                            @update:modelValue="changed">
                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                <input v-bind:class="{'form-control' : true,'is-invalid':errors[0]}" :value="inputValue" v-on="inputEvents"
                                                   />
                                                <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em 
                                                            class="fa fa-calendar"></em></span></div>
                                            </template>
                                        </v-date-picker>  
                                        <span v-if="errors[0]" :name="key.COLUMN_NAME" class="invalid-feedback d-block">{{ errors[0]}}</span>   </Field>              
                                    </div>
                                    <Field  :name="key.COLUMN_NAME+'_'+ index" :rules="{ 'required': key.IS_REQUIRED }" :label="GetFormSchemaFieldName(key.COLUMN_NAME,(key.COLUMN_NAME.replace(/_/g, ' ').replace(/id/, '').replace(/lookup/,''))[0].toUpperCase() + (key.COLUMN_NAME.replace(/_/g, ' ').replace(/id/, '').replace(/lookup/,'')).substring(1))"
                                        v-slot="{errors}">
                                    <select v-if="key.ACTUAL_DATA_TYPE=='select' && key.COLUMN_NAME!='existing_client_id_lookup' && key.PICKLIST_OPTIONS == 'Lookup'" 
                                     v-bind:name="key.COLUMN_NAME" v-model="item[key.COLUMN_NAME]"
                                        v-bind:class="{'form-control' : true,'is-invalid':key.ACTUAL_DATA_TYPE=='select'&& errors[0]}"                                                                                                                  
                                        v-bind:id="key.COLUMN_NAME+'_'+ index">
                                        <option value="">{{$t('Select')}}</option>
                                        <option v-for="(option,optionIndex) in MakeNormalArray(key.SELECT_OPTIONS)" :key="optionIndex"
                                            v-bind:selected="item[key.COLUMN_NAME]==option.value"
                                            v-bind:value="option.value">{{option.name}}</option>
                                    </select>
                                    <select  v-bind:name="key.COLUMN_NAME+'_'+ index" v-model="item[key.COLUMN_NAME]"
                                        v-bind:class="{'form-control' : true,'is-invalid':key.ACTUAL_DATA_TYPE=='select' &&  errors[0],
                                        'd-none':item.existing_client=='0' && key.COLUMN_NAME=='existing_client_id_lookup'}"                                       
                                        v-if="key.ACTUAL_DATA_TYPE=='select' && key.COLUMN_NAME=='existing_client_id_lookup' && key.PICKLIST_OPTIONS == 'Lookup'"                                       
                                        v-bind:id="key.COLUMN_NAME+'_'+ index">
                                        <option value="">{{$t('Select')}}</option>
                                        <option v-for="(option,optionIndex1) in MakeNormalArray(key.SELECT_OPTIONS)" :key="optionIndex1"
                                            v-bind:selected="item[key.COLUMN_NAME]==option.value"
                                            v-bind:value="option.value">{{option.name}}</option>
                                    </select>
                                    <select  v-bind:name="key.COLUMN_NAME+'_'+ index" v-model="item[key.COLUMN_NAME]"
                                        v-bind:class="{'form-control' : true,'is-invalid':key.ACTUAL_DATA_TYPE=='select' && errors[0]}"                                       
                                        v-if="key.ACTUAL_DATA_TYPE=='select' && key.COLUMN_NAME!='existing_client_id_lookup' && key.PICKLIST_OPTIONS != 'Lookup'"                                        
                                        v-bind:id="key.COLUMN_NAME+'_'+ index">
                                        <option value="">{{$t('Select')}}</option>
                                        <option v-for="option in MakeArray(key.SELECT_OPTIONS, 'Select')" :key="option"
                                            v-bind:selected="trimValue(item[key.COLUMN_NAME])==trimValue(option.value)"
                                            v-bind:value="option.value">{{option.name}}</option>
                                    </select>
                                    <span v-if="key.ACTUAL_DATA_TYPE=='select' &&  errors[0]" :name="key.COLUMN_NAME" class="invalid-feedback">{{errors[0]}}</span>
                                </Field>
                                <Field v-if="TextBoxFor.indexOf(key.ACTUAL_DATA_TYPE) != -1"
                                :name="key.COLUMN_NAME+'_'+ index" :rules="{ 'required': key.IS_REQUIRED }" :label="$t(key.COLUMN_NAME)"
                                         v-slot="{ errors}">
                                    <input v-bind:class="{'form-control':true}" type="text"
                                        v-bind:id="key.COLUMN_NAME+'_'+ index" v-model="item[key.COLUMN_NAME]"
                                        v-bind:name="key.COLUMN_NAME+'_'+ index"                                        
                                        v-on:input="SetValues($event,item)"
                                        v-on:keyPress="CheckValidName($event,item,key.COLUMN_NAME)" />
                                        <span v-if="errors[0]" :name="key.COLUMN_NAME" class="invalid-feedback">{{errors[0]}}</span>
                                    </Field>
                                    <div class="form-control pl-0 border-0 bg-transparent"
                                        v-if="key.ACTUAL_DATA_TYPE=='checkbox' ">
                                        <div class="form-check form-check-inline">
                                            <div class="custom-control custom-checkbox"
                                                v-for="(option,optionIndex1) in MakeArray(key.SELECT_OPTIONS,key.ACTUAL_DATA_TYPE)" :key="optionIndex1">
                                                <Field :name="item[key.COLUMN_NAME]" :rules="{ 'required': key.IS_REQUIRED }" :label="GetFormSchemaFieldName(key.COLUMN_NAME,key.COLUMN_NAME)"
                                         v-slot="{ errors }">
                                                <input  v-bind:id="'chk_'+ index+ '_' + option.value"
                                                    class="dynamic custom-control-input" v-model="item[key.COLUMN_NAME]"
                                                    v-bind:name="'chk_'+ key.COLUMN_NAME+'_'+index"
                                                    v-bind:data-resource-id="option.name" v-bind:value="option.value"
                                                    type="radio">
                                                <span v-if="key.ACTUAL_DATA_TYPE=='checkbox' &&  errors[0]" :name="item[key.COLUMN_NAME]" class="invalid-feedback">{{errors[0]}}</span> 
                                                </Field>
                                                <label
                                                    class="custom-control-label universal-custom-control-label pl-1 pr-3 dynamic"
                                                    v-bind:for="'chk_'+ index+ '_' + option.value">{{option.name}}</label>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="form-control pl-0 border-0 bg-transparent"
                                        v-if="key.ACTUAL_DATA_TYPE=='radio'">
                                        <div class="form-check form-check-inline">
                                            <div class="form-check form-check-inline custom-control mx-2 custom-radio"
                                                v-for="option in MakeArray(key.SELECT_OPTIONS,key.ACTUAL_DATA_TYPE)" :key="option">
                                                <Field :name="GetFormSchemaFieldName(key.COLUMN_NAME,key.COLUMN_NAME)" :rules="{ 'required': key.IS_REQUIRED }" v-slot="{ errors }">
                                                <input  type="radio" v-bind:id="'rdo_'+ index+ '_' + option.value +''"
                                                    v-model="item[key.COLUMN_NAME]"
                                                    v-bind:name="'rdo_'+ key.COLUMN_NAME+'_'+index"
                                                    class="dynamic form-check-input custom-control-input"
                                                    v-bind:data-resource="option.name"
                                                    v-bind:checked="option.value == item[key.COLUMN_NAME]"
                                                    v-bind:value="option.value"
                                                    v-on:change="showDependentItem(item,option.value,index)"/>
                                                 <span v-if="key.ACTUAL_DATA_TYPE=='radio' && errors[0]" :name="item[key.COLUMN_NAME]" class="invalid-feedback">{{errors[0]}}</span> 
                                                    </Field>
                                                <label
                                                    class="custom-control-label universalradio-custom-control-label pl-2 dynamic"
                                                    v-bind:for="'rdo_'+ index+ '_' + option.value +''">{{option.name}}</label>
                                            </div>
                                        </div>
                                    </div>
                                    
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <div v-if="noRecord" class="text-center text-danger no-record" style="padding:20px;">
                        {{$t('NoRecordfound')}} </div>
                </div>
           

<div class="col-12 mt-3 p-0">
  <div class="row flex-row-reverse">
  <div class="col-lg-6 text-right">
  <a href="javascript:;" v-bind:class="{disabled: submited}"
                            class="btn btn-success formbtn widthhalf" v-on:click="SubmitData( valid,errors)"
                            :title="$t('Save')"><em  class="fa fa-save pr-2"></em>{{$t('Save')}}</a>
                        <a href="/CRM/Index" :title="$t('BacktoList')"
                            class="btn btn-secondary formbtn m-1 pr-5 pr-sm-5 pr-md-4 pr-lg-4 pr-xl-4"><em 
                                class="fa fa-angle-double-left pr-2"></em>{{$t('BacktoList')}}</a>
                        <a href="/CRM/ImportLeads" class="btn btn-danger formbtn widthhalf" :title="$t('Cancel')"><em 
                                class="fa fa-close pr-2"></em>{{$t('Cancel')}}</a> </div>
  <div class="col-lg-6"><div class="py-2"><small class="text-danger">{{$t('MandatoryString')}}</small></div></div>
   </div></div> </div>

            </Form>
        </div>
    </div>
   
</template>
<script>
import DataService from "../../services/DataService";   
export default {
    data(){
        return {
            isLoading: false,
            CompanyId: null,
            loggedInUserId:null,
            batchid: this.$route.params.batchid,
            ImportLeadData: null,
            Headers: null,
            options: null,           
            noRecord: false,
            submited: false,
            TextBoxFor: ["text", "decimal", "int", "bigint", "Url", "phone"],
        }
    } ,
        created: function () {
            this.CompanyId= this.GetUserInfo.CompanyId;
            this.loggedInUserId=this.GetUserInfo.ID;
            this.FetchData();
        },
        methods: {
            CheckValidName: function (event, item, ColName) {
                var regex = /^[A-Za-z]+$/
                var isVali = regex.test(item.firstname + event.key);
                if ((ColName == "firstname" || ColName == "lastname") && !isVali) {
                    event.preventDefault();
                }
            },
            hideShowClientDropDown: function () {
                var vueObj = this;
                for (var i = 0; i < vueObj.ImportLeadData.length; i++) {
                    for (var j = 0; j < vueObj.Headers.length; j++) {
                        if (vueObj.Headers[j].TABLE_COLUMN_NAME == "is_existing_client") {
                            var value = vueObj.ImportLeadData[i][vueObj.Headers[j].COLUMN_NAME];
                            var setValue = 0;
                            if (value == "Yes" || value == "True" || value == "1") {
                                setValue = 1;
                            }
                            if (setValue == 0) {
                                $("#existing_client_id_lookup_" + i).addClass("d-none");
                            }
                            else {
                                $("#existing_client_id_lookup_" + i).removeClass("d-none");
                            }
                        }
                    }
                }
            },
            checkIfIsInvalid: function (item) {
                if (typeof item.checkIfIsInvalid !== 'undefined') {
                    return item.checkIfIsInvalid;
                } else {
                    return false;
                }
            },
            isNumber: function (evt) {
                evt = (evt) ? evt : window.event;
                var charCode = (evt.which) ? evt.which : evt.keyCode;
                if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                    evt.preventDefault();
                } else {
                    return true;
                }
            },
            MakeArray: function (value, type) {
                var array = [];
                var arr = String(value).split(',');
                if (type == "radio" || type == "checkbox") {
                    if (arr.length > 0) {
                        for (var item of arr) {
                            if (item.split("|").length > 1) {
                                var person = { name: item.split("|")[0], value: item.split("|")[1] };
                                array.push(person);
                            }
                            else {
                                var person = { name: item, value: item };
                                array.push(person);
                            }
                        }
                    }
                }
                else {
                    if (arr.length > 0) {
                        for (var item of  arr) {
                            var person = { name: item, value: item };
                            array.push(person);
                        }
                    }
                }
                return array;
            },
            trimValue: function (value) {
                return value.trim();
            },
            showDependentItem: function (item, value, chilDIndex) {
                var setValue = 0;
                if (value == "Yes" || value == "True" || value == "1") {
                    setValue = 1;
                }
                if (setValue == 0) {
                    $("#existing_client_id_lookup_" + chilDIndex).addClass("d-none");
                }
                else {
                    $("#existing_client_id_lookup_" + chilDIndex).removeClass("d-none");
                }
            },
            GetValidationLocal: function (object) {
                var obj = {};
                if (object.is_required) {
                    obj['required'] = true;
                }
                if (object.ACTUAL_DATA_TYPE == "decimal") {
                    obj['decimal'] = 2;
                }
                if (object.length > 0) {
                    obj['max'] = object.length;
                }
                if (object.ACTUAL_DATA_TYPE == "text") {
                    obj['regex'] = '^[a-zA-Z\s]+$';
                }
                if (object.ACTUAL_DATA_TYPE == "date") {
                    obj['date'] = String(Globalize.culture().calendar.patterns.d).toUpperCase();
                }
                if (object.ACTUAL_DATA_TYPE == "int" || object.ACTUAL_DATA_TYPE == "bigint") {
                    obj['numeric'] = true;
                }
                if (object.ACTUAL_DATA_TYPE == "url") {
                    obj['url'] = 'require_protocol';
                }
                return obj;
            },
            FetchData: function () {
                this.isLoading=true;
                var vm = this;
                var param =`companyId=${this.CompanyId}&loggedInUserId=${this.loggedInUserId}&batchid=${this.batchid}`;
                DataService.Importlead(param)
                .then(response => {
                    if (response.data != null) {
                        vm.Headers = [];
                        vm.Headers = response.data.schema;
                        if (response.data.data.length > 0) {
                            vm.ImportLeadData = [];
                            vm.ImportLeadData = response.data.data;
                            vm.noRecord = false;
                            setTimeout(function(){
                                var frmFields = {} 
                                vm.ImportLeadData.forEach((item, index) => {
                                    vm.Headers.forEach((key, KIndex) => {
                                        if (key.ACTUAL_DATA_TYPE == 'date') {
                                            frmFields[key.COLUMN_NAME] = item[key.COLUMN_NAME]
                                        }
                                        if (key.ACTUAL_DATA_TYPE == 'select' && key.COLUMN_NAME != 'existing_client_id_lookup' && key.PICKLIST_OPTIONS == 'Lookup' ||
                                            key.ACTUAL_DATA_TYPE == 'select' && key.COLUMN_NAME == 'existing_client_id_lookup' && key.PICKLIST_OPTIONS == 'Lookup' ||
                                            key.ACTUAL_DATA_TYPE == 'select' && key.COLUMN_NAME != 'existing_client_id_lookup' && key.PICKLIST_OPTIONS != 'Lookup') {
                                            frmFields[key.COLUMN_NAME] = item[key.COLUMN_NAME]
                                        }
                                        if (vm.TextBoxFor.indexOf(key.ACTUAL_DATA_TYPE) != -1) {
                                            frmFields[key.COLUMN_NAME + '_' + index] = item[key.COLUMN_NAME]
                                        }
                                        if (key.ACTUAL_DATA_TYPE == 'checkbox') {
                                            frmFields['chk_' + key.COLUMN_NAME + '_' + index] = item[key.COLUMN_NAME]
                                        }
                                        if (key.ACTUAL_DATA_TYPE == 'radio') {
                                            frmFields['rdo_' + key.COLUMN_NAME + '_' + index] = item[key.COLUMN_NAME]
                                        }
                                    })
                                })
                                console.log(frmFields);
                                    // vm.$refs.form.resetForm({ values: frmFields });
                            },2000)
                        } else {
                            vm.noRecord = true;
                            vm.ImportLeadData = [];
                        }
                    }
                    else {
                        vm.noRecord = true;
                        vm.ImportLeadData = [];
                        vm.Headers = [];
                    }
                    setTimeout(function () { vm.hideShowClientDropDown(); }, 100)
                })
                vm.isLoading=false;
            },
            GetLocalizedValue: function (fieldName, displayName) {
                if (this.isBlank(displayName)) {
                    var chk = Globalize.localize(String(fieldName).toUpperCase());
                    if (chk || /^\s*$/.test(chk)) {
                        return chk;
                    }
                    chk = Globalize.localize(String(fieldName));
                    if (chk || /^\s*$/.test(chk)) {
                        return chk;
                    }
                    return fieldName;
                }
                else {
                    var chk = Globalize.localize(String(displayName).toUpperCase());
                    if (chk || /^\s*$/.test(chk)) {
                        return chk;
                    }
                    chk = Globalize.localize(String(displayName));
                    if (chk || /^\s*$/.test(chk)) {
                        return chk;
                    }
                    return displayName;
                }
            },
            isBlank: function (str) {
                return (!str || /^\s*$/.test(str));
            },
            stringToDate: function (_date, _format, _delimiter) {
                var formatLowerCase = _format.toLowerCase();
                var formatItems = formatLowerCase.split(_delimiter);
                var dateItems = _date.split(_delimiter);
                var monthIndex = formatItems.indexOf("mm");
                var dayIndex = formatItems.indexOf("dd");
                var yearIndex = formatItems.indexOf("yyyy");
                var month = dateItems[monthIndex] < 10 ? "0" + dateItems[monthIndex] : dateItems[monthIndex];
                var formatedDate = new Date(dateItems[yearIndex], month, dateItems[dayIndex]);
                var months = formatedDate.getMonth();
                var days = formatedDate.getDate();
                if (formatedDate.getMonth() < 9 && formatedDate.getMonth() > 0) {
                    months = "0" + months;
                }else{
                    months=month;
                }
                if (formatedDate.getDate() < 9) {
                    days = "0" + days;
                }
                return formatedDate.getFullYear() + "-" + months + "-" + days + "T00:00:00";
            },
            validateSpecialCharacters: function (roleName) {   //added by ejaz to check specail characters on Name 20/07/2022
                const re = /^[A-Za-z0-9 ]+$/;
                return re.test(String(roleName).toLowerCase());
            },
            validateEmail: function (email) {
                const re =/^[a-zA-Z0-9.!#$%&'*+\/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
                return re.test(email);
            },
            validatePhoneNumber: function (value) {
                const re = /^[0-9 ()-]+$/
                return re.test(String(value).toLowerCase());
            },
            validatedecimal: function (value) {
                const re = /^(\d+)?([.]?\d{0,2})?$/
                return re.test(String(value).toLowerCase());
            },
            checkValidation: function () {
                var vueObj = this;
                var err = true;
                var phoneNumberFields = ["contact_number", "home_phone", "business_phone"];
                for (var i = 0; i < vueObj.ImportLeadData.length; i++) {
                    for (var j = 0; j < vueObj.Headers.length; j++) {
                        if (vueObj.Headers[j].TABLE_COLUMN_NAME == "is_existing_client") {
                            if (vueObj.ImportLeadData[i][vueObj.Headers[j].COLUMN_NAME] == '1') {
                                if ($.trim(vueObj.ImportLeadData[i]["existing_client_id_lookup"]) == "") {
                                    $("#existing_client_id_lookup_" + i).addClass("is-invalid");
                                    err = false;
                                }
                            } else {
                                $("#existing_client_id_lookup_" + i).addClass("is-invalid");
                            }
                        } else if (vueObj.Headers[j].TABLE_COLUMN_NAME == "client_first_name") {
                            if (!vueObj.validateSpecialCharacters(vueObj.ImportLeadData[i][vueObj.Headers[j].COLUMN_NAME])) {
                                $("#" + vueObj.Headers[j].COLUMN_NAME + "_" + i).addClass("is-invalid");
                                err = false;
                            } else {
                                $("#" + vueObj.Headers[j].COLUMN_NAME + "_" + i).removeClass("is-invalid");
                            }
                        }else if (vueObj.Headers[j].TABLE_COLUMN_NAME == "client_last_name") {
                            if (!vueObj.validateSpecialCharacters(vueObj.ImportLeadData[i][vueObj.Headers[j].COLUMN_NAME])) {
                                $("#" + vueObj.Headers[j].COLUMN_NAME + "_" + i).addClass("is-invalid");
                                err = false;
                            } else {
                                $("#" + vueObj.Headers[j].COLUMN_NAME + "_" + i).removeClass("is-invalid");
                            }
                        } else if (vueObj.Headers[j].TABLE_COLUMN_NAME == "client_email") {
                            if (!vueObj.validateEmail(vueObj.ImportLeadData[i][vueObj.Headers[j].COLUMN_NAME])) {
                                $("#" + vueObj.Headers[j].COLUMN_NAME + "_" + i).addClass("is-invalid");
                                err = false;
                            } else {
                                $("#" + vueObj.Headers[j].COLUMN_NAME + "_" + i).removeClass("is-invalid");
                            }
                        } else if (phoneNumberFields.includes(vueObj.Headers[j].TABLE_COLUMN_NAME.toLowerCase())) {
                            if (!vueObj.validatePhoneNumber(vueObj.ImportLeadData[i][vueObj.Headers[j].COLUMN_NAME])) {
                                $("#" + vueObj.Headers[j].COLUMN_NAME + "_" + i).addClass("is-invalid");
                                err = false;
                            } else {
                                $("#" + vueObj.Headers[j].COLUMN_NAME + "_" + i).removeClass("is-invalid");
                            }
                        }
                        else {
                            if (vueObj.Headers[j].IS_REQUIRED && $.trim(vueObj.ImportLeadData[i][vueObj.Headers[j].COLUMN_NAME]) == "" && vueObj.Headers[j].COLUMN_NAME != "existing_client_id_lookup") {
                                $("#" + vueObj.Headers[j].COLUMN_NAME + "_" + i).addClass("is-invalid");
                                err = false;
                            } else {
                                $("#" + vueObj.Headers[j].COLUMN_NAME + "_" + i).removeClass("is-invalid");
                            }
                        }
                        if (vueObj.Headers[j].COLUMN_NAME == "lead_date") {
                            if(typeof(vueObj.ImportLeadData[i].lead_date)=='string'){
                                vueObj.ImportLeadData[i].lead_date = (vueObj.ImportLeadData[i].lead_date).split(' ')[0];
                            }
                            else{
                                vueObj.ImportLeadData[i].lead_date =vueObj.$options.filters.formatDate(vueObj.ImportLeadData[i].lead_date).split(' ')[0];
                            }
                            var split = vueObj.ImportLeadData[i].lead_date.split("/");
                            var dt = vueObj.ImportLeadData[i].lead_date;
                            if (split.length > 1) {
                                dt = vueObj.stringToDate(vueObj.ImportLeadData[i].lead_date, "mm/dd/yyyy", "/");
                            }else{
                                dt=vueObj.$options.filters.formatDate(vueObj.ImportLeadData[i].lead_date);
                            }
                            vueObj.ImportLeadData[i].lead_date = dt;
                        }
                        if (vueObj.Headers[j].TABLE_COLUMN_NAME == "money_value") {
                            if (!vueObj.validatedecimal(vueObj.ImportLeadData[i][vueObj.Headers[j].COLUMN_NAME])) {
                                $("#" + vueObj.Headers[j].COLUMN_NAME + "_" + i).addClass("is-invalid");
                                err = false;
                            }
                            else {
                                $("#" + vueObj.Headers[j].COLUMN_NAME + "_" + i).removeClass("is-invalid");
                            }
                        }
                    }
                }
                return err;
            },
            SubmitData( valid,errors) {
                var vueObj = this;
                if (vueObj.checkValidation()) { 
                        
              vueObj.isLoading=true;
                    var fullJson = this.ImportLeadData;
                    var postJSON = {
                        data: JSON.stringify(fullJson),
                        CompanyId: this.CompanyId,
                        BatchId: this.batchid
                    };                   
                    var objectPostString = JSON.stringify(postJSON);                 
                    DataService.SaveImportlead(objectPostString).then(function (response) {
                        if (response.data[0].Status == "Success") {                          
                            vueObj.ShowAlert(vueObj.$t('UpdatedSuccess',[vueObj.$t('ImportLeads')]), 'success', true,vueObj.$t("Alert"));
                            
                            vueObj.$router.push({name:'CRMLead'}); 
                        }
                        else {
                            vueObj.ShowAlert(vueObj.$t('AddedError',[vueObj.$t('Lead')]), 'warning', true,vueObj.$t("Alert"));
                        }
                        vueObj.isLoading=false;
                    });
                    }
                    else {
                    vueObj.ShowAlert(vueObj.$t('PleaseCorrectHighlightedErrors'), 'warning', true,vueObj.$t("Alert"));
                    }
            },
            SetValues: function ($event, item, type) {
                if (item.dt_code == "date" || item.dt_code == "decimal") {
                    if (type == "second") {
                        var value = item.second_value;
                        item.second_value = value;
                    }
                    else {
                        var value = item.value;
                        item.value = value;
                    }
                }
                else {
                    item.value = $event.target.value;
                }
             
            },
        },
        
}
</script>